<style>
 @import "../assets/css/background_broadcrumb.css";
 
</style>
<template>
    <div>
        <div class="cover_case">
          <v-img :src="`${getHeader[0].images[0].link}`" cover>
            <v-overlay absolute class="bottom_card">
                <v-container>
                <v-card class="card_background" flat color="transparent" left>
                        <v-card-title class="title"><span>case studies</span></v-card-title>
                        <v-card-subtitle class="desc">
                            <p>
                              {{getHeader[0].full_text}}
                            </p>
                        </v-card-subtitle>
                   </v-card>
                   <v-breadcrumbs :items="items2" class="broadcrumbs"></v-breadcrumbs>
                </v-container>
            </v-overlay>
          </v-img>
       </div>
          <v-img :src="require('@/assets/images/services/transperant-yellow-points-wide-rec.png')" max-height="200"></v-img>
       <section>
         <CaseStudyList></CaseStudyList>
       </section>
   
    </div>
  </template>
  <script>
  import CaseStudyList from '@/components/CaseStudiesList2.vue';
 import { mapGetters, mapActions } from "vuex";
  export default{
   name:"CaseStudiesView",
   components:{
     CaseStudyList
   },
   data(){
      return{
         items2: [
            {
               text: 'Home',
               disabled: false,
               href: '/',
            },
            {
               text: 'Case Studies',
               disabled: false,
               href: '/casestudies',
            },
         ]
      }
   },
   methods: {
    ...mapActions(["fetchTopics"]),
   },
   computed:{
    ...mapGetters(["getHeader"])
   },
   mounted() {
       let caseStudiesHeader={
         topic_type_id:40,
         cat:"header"
      }
      this.fetchTopics(caseStudiesHeader);
   },
  }
  </script>